$color: #00cc99;
@import "reset";
@import "header";

h2 {
  font-size: 64px;
  text-align: center;
}

.bold-txt-red {
  text-align: center;
  font-weight: 900;
  color: $dark-red;
}

.bold-txt {
  font-size: 48px;
  line-height: 72px;
}
.bold-txt-mb{
  margin-bottom: 48px;
}
.banner-wrap {

  background-size: contain;
  background-image: url("../images/home-header.png");
}

.banner {
  width: 100%;
  height: 787px;
  display: flex;
  align-items: center;
  justify-content: end;
}

.banner-txt {
  text-align: end;
  color: $dark-red;

  h1 {
    font-size: 36px;
  }

  button {
    padding: 6px 12px;
    color: $white;
    background-color: $dark-red;
  }
}
[class*=section]{
  padding: 80px 0;
}
.section2 {
  color: $dark-red;
  background-color: $y-white;

  h2 {
    margin-bottom: 32px;
  }

  li {
    width: 260px;
  }

  li>div {
    margin: 0 auto;
    padding: 8.08px;
    width: 100px;
  }

  h3 {
    text-align: center;
    margin: 16px 0 8px;
    font-size: 24px;
    font-weight: 900;
  }

  p {
    color: $black;
    font-size: 16px;
  }
}
.section3 {
  p {
    margin-top: 8px;
  }
  .pic {
    max-width: 416px;
  }
}
.section4 {
  background-image: url("../images/index-bg.png");
  h2 {
    color: $white;
  }
  .pic {
    width: 50%;
    p {
      background-color: #fff;
      padding: 8px 0;
    }
  }
}